html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.container-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2rem;
}

p {
  font-size: 1.5rem;
  color: #6b7280;
  line-height: 1.8;
}

.page-description {
  text-align: center;
}

.tagline {
  font-family: "Inter", sans-serif;
  font-weight: 900;
  color: #000;
  font-size: 2rem;
  text-align: center;
}

/** Media Queries **/
.form {
  margin-top: 3rem;
}
.form .field {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.form .field label {
  flex: 0 0 10rem;
}
.form .field input {
  flex: 1;
  border: none;
  outline: none;
  background-color: #d8dadf;
  padding: 1rem;
}

.actions {
  margin-top: 3rem;
  display: flex;
  justify-content: space-between;
}
.actions a {
  color: #000;
}
@media (max-width: 768px) {
  .actions {
    flex-direction: column;
    text-align: center;
  }
  .actions a {
    margin-bottom: 2rem;
  }
  .actions a:last-of-type {
    margin-bottom: 0;
  }
}

.alert {
  display: block;
  margin: 1rem 0;
  padding: 1rem;
  color: #FFFFFF;
  font-weight: 900;
  font-size: 1.5rem;
  text-transform: uppercase;
}
.alert.error {
  background-color: #ffdcdc;
  color: #a90000;
  border-left: 0.5rem solid #a90000;
}
.alert.success {
  background-color: #abffab;
  color: #007800;
  border-left: 0.5rem solid #007800;
}

.login .uptask {
  background: linear-gradient(to right, #0891B2 0%, white 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  font-size: 6rem;
  margin-top: 3rem;
}
@media (max-width: 480px) {
  .login .uptask {
    font-size: 4rem;
  }
}
.login input[type=submit] {
  background-color: #0891B2;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: auto;
}
@media (max-width: 768px) {
  .login input[type=submit] {
    display: block;
  }
}
.login input[type=submit]:hover {
  background-color: #066981;
  cursor: pointer;
}
.login .actions a {
  color: #0891B2;
}

