.dashboard {
  background-color: #f3f4f6;
  display: flex;
  height: 100vh;
}
@media (max-width: 768px) {
  .dashboard {
    flex-direction: column;
  }
}
.dashboard .principal {
  flex: 1;
  overflow: scroll;
}
.dashboard .principal .content {
  padding: 2rem;
}
.dashboard .principal .content .page-name {
  font-size: 2.8rem;
  text-align: center;
}
.dashboard .principal .form input:not([type=submit]) {
  background-color: #FFFFFF;
}
.dashboard .principal .form input[type=submit] {
  background-color: #4338CA;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
