.sidebar {
  background-color: #4338CA;
  padding: 2rem;
}
.sidebar h2 {
  color: #FFFFFF;
  margin: 0;
  text-align: center;
}
.sidebar .sidebar-nav {
  margin-top: 5rem;
}
@media (max-width: 768px) {
  .sidebar .sidebar-nav {
    margin-top: 1rem;
  }
}
.sidebar .sidebar-nav a {
  color: #FFFFFF;
  display: block;
  padding: 1rem;
  margin-bottom: 0.2rem;
  border-radius: 0.3rem;
  transition: background-color 0.3s ease;
}
.sidebar .sidebar-nav a:hover, .sidebar .sidebar-nav a.active {
  background-color: #28217b;
}

.bar {
  background-color: #FFFFFF;
  padding: 1rem 2rem;
  box-shadow: 1px 12px 9px -13px rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.bar p {
  margin: 0;
  font-size: 1.4rem;
}
.bar p span {
  font-weight: 700;
  color: #4338CA;
}
.bar .logout {
  background-color: #28217b;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: auto;
  font-size: 1.2rem;
  padding: 1rem;
  border-radius: 0.3rem;
}
@media (max-width: 768px) {
  .bar .logout {
    display: block;
  }
}
.bar .logout:hover {
  background-color: #1b1653;
  cursor: pointer;
}

.no-proyects a {
  color: #4338CA;
}

.list-proyects {
  list-style: none;
  padding: 0;
  margin-top: 3rem;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(3, 1fr);
}
@media (max-width: 768px) {
  .list-proyects {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (max-width: 480px) {
  .list-proyects {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(1, 1fr);
  }
}
.list-proyects .proyect a {
  color: #FFFFFF;
  background-color: #564ccf;
  min-height: 12rem;
  display: flex;
  align-items: center;
  padding: 2rem;
  line-height: 2;
  border-radius: 1rem;
  transition: 0.3s ease all;
}
.list-proyects .proyect a:hover {
  background-color: #3b31b8;
}

.new-task-container {
  display: flex;
  justify-content: center;
}
.new-task-container .add-task {
  background-color: #0891B2;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: auto;
}
@media (max-width: 768px) {
  .new-task-container .add-task {
    display: block;
  }
}
.new-task-container .add-task:hover {
  background-color: #066981;
  cursor: pointer;
}

.list-tasks {
  list-style: none;
  padding: 0;
  margin-top: 3rem;
}
.list-tasks .no-tasks {
  text-align: center;
}

.task {
  padding: 0.5rem 2rem;
  border-bottom: 2px solid #e4e7eb;
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.task:last-of-type {
  border: none;
}
.task p {
  font-size: 1.4rem;
}
.task .options {
  display: flex;
  gap: 1rem;
}
.task .options button {
  border: none;
  padding: 0.5rem 1rem;
  font-size: 1.4rem;
  font-weight: 600;
  border-radius: 1.5rem;
}
.task .options button:hover {
  cursor: pointer;
}
.task .options .completa {
  background-color: #2563EB;
  color: #FFFFFF;
}
.task .options .pendiente {
  background-color: #F59E0B;
  color: #FFFFFF;
}
.task .options .delete-task {
  background-color: #a90000;
  color: #FFFFFF;
}

.modal {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.modal .form {
  max-width: 60rem;
  padding: 2rem;
  background-color: #FFFFFF;
  position: relative;
  transform: translateY(-30rem);
  opacity: 0;
  border-radius: 1rem;
  transition: all 0.3s ease;
}
.modal .form legend {
  font-size: 2rem;
  text-align: center;
  font-weight: bold;
  margin-bottom: 2rem;
  display: block;
}
.modal .form .options {
  display: flex;
  justify-content: space-between;
}
.modal .form .options .new-task-submit {
  background-color: #4338CA;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: auto;
}
@media (max-width: 768px) {
  .modal .form .options .new-task-submit {
    display: block;
  }
}
.modal .form .options .new-task-submit:hover {
  background-color: #352ca3;
  cursor: pointer;
}
.modal .form .options .close-modal {
  background-color: #F59E0B;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: auto;
}
@media (max-width: 768px) {
  .modal .form .options .close-modal {
    display: block;
  }
}
.modal .form .options .close-modal:hover {
  background-color: #c57f08;
  cursor: pointer;
}
.modal .form.animate {
  top: 50%;
  margin: 0 auto;
  transform: translateY(-50%);
  opacity: 1;
}
.modal .form.close {
  transform: translateY(-200rem);
  opacity: 0;
}